@import './variables.css';

html {
    font-family: 'helvetica_neuethin', arial, sans-serif;
    letter-spacing: 1px;
    line-height: 1.28571428571429;
}

h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
    font-weight: normal;
    text-transform: uppercase;
    margin-top: 0;
    letter-spacing: 1px;
    line-height: 1;
    margin: 0;
}

html {
    font-size: $fontSizeMobile;
}

h1, .h1 {
    font-size: calc($fontSizeMobile * 4); // 48px
}

h2, .h2 {
    font-size: calc($fontSizeMobile * 4); // 48px
    margin-bottom: calc($fontSizeMobile * 2); // 24px
}

h3, .h3 {
    font-size: calc($fontSizeMobile * 2); // 24px
    margin-bottom: $fontSizeMobile; // 12px
}

#main {
    h1, .h1 {
        margin-bottom: calc($fontSizeMobile * 4); // 48px
    }
}

ul, ol, p {
    margin: 0 0 $fontSizeMobile;
}

a {
    &:link,
    &:visited,
    &:hover,
    &:active {
        color: $foreground;
        cursor: pointer;
    }
    &:hover,
    &:active {
        text-decoration: none;
    }
}

h1, .h1 {
    a {
        text-decoration: none;
    }
}

.inverse {
    a {
        &:link,
        &:visited{
            color: $inverseForeground;
        }
        &:hover,
        &:active {
            color: $tpbcYellow;
        }
    }
}

@media (min-width: $breakpointTablet) {
    html {
        font-size: $fontSizeTablet;
    }

    h1, .h1 {
        font-size: calc($fontSizeTablet * 7.14285714285714); // 100px
    }
    h2, .h2 {
        font-size: calc($fontSizeTablet * 3.42857142857143); // 48px
        margin-bottom: calc($fontSizeTablet * 3.42857142857143 - $fontSizeTablet); // 34px
    }

    h3, .h3 {
        font-size: calc($fontSizeTablet * 1.71428571428571); // 24px
        margin-bottom: $fontSizeTablet; // 14px
    }

    #main {
        h1, .h1 {
            margin-bottom: calc($fontSizeTablet * 3.42857142857143); // 48px
        }
    }

    ul, ol, p {
        margin: 0 0 $fontSizeTablet; // 14px
    }
    #content {
        h1, .h1 {
            font-size: calc($fontSizeTablet * 4.28571428571429); // 60px
        }
    }
}



